}
.ItemPage h2 {
   padding: 20px; 
   font-size: 25px;
   font-family: 'Nunito', sans-serif;
}
.item {
    padding: 5px; 
    font-size: 20px;
    font-family: 'Nunito', sans-serif;
    margin: 10px;
    box-shadow : 0px 2px  rgb(189, 189, 218); 
    display: flex;
   
 
}
.item-name {
  
    width: 20%;
}
.price {
    margin-left: 40%;
    width: 15%;
    padding: 10px;
   
    text-align: center;
}
