    .add {
        border: 2px solid wheat;
        background-color: wheat;
        border-radius: 5px;
        margin-left: 20px;
        cursor: pointer;
        width:50px;
    }
}
@media only screen and (min-width:500px) and (max-width:3000px){
    
 .add {
