    width: 20%;
   }
}
.App {
    display: flex;
    width: 100%;
    flex-direction: column;
    flex-wrap: wrap;
    justify-content: center;
    font-size: 25px;
    font-family: 'Nunito', sans-serif;
   
}
.CartPage{
    border: 2px solid #EAEDED;
    border-radius: 10px;
    width: 98%;
    height: 50%;
    /* justify-content: center;
    align-items: center; */
    margin: 10px ;
    
}
.CartPage h2 {
    padding: 20px; 
    font-size: 25px;
    font-family: 'Nunito', sans-serif;
 }
.item-count {
    width: 5%;
    padding: 10px;
   text-align: center;
}
.checkout {
    font-size: 25px;
    font-family: 'Nunito', sans-serif;
    padding: 20px;
    margin-top: 10px;
    cursor: pointer;
    outline: none;
}
.total {
    float: right;
    padding: 20px;
    margin-top: 10px;
    margin-right: 15%;
}

.CheckOutPage {
    border: 2px solid #EAEDED;
    border-radius: 10px;
    width: 98%;
    height: 50%;
    margin: 10px ;
    padding : 20px;
    display: flex;
    flex-direction: column;
}
 p {
     padding: 10px 15px;
     font-weight: bold;
     border: 0px solid gray;
     box-shadow: 0px 2px black;
     font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
 }
 form {
     border: 1px solid rgb(241, 234, 234);
     border-radius: 10px;
     margin-top: 20px;
     padding: 10px;
 }
.field-group {
  margin: 15px 5px;
   display: flex;
}
@media only screen and (min-width:300px) and (max-width:500px){
    .field-group{
        display: flex;
        flex-direction: column;
       
    }
    .field-group label {
        width: 200px;
    }
    .field-group input , .field-group option , .field-group select {
        font-size: 25px;
        font-family: 'Nunito', sans-serif;
        border-radius: 2px ;
        box-shadow: 0px 2px rgb(8, 5, 5);
        width: 75vw;
        border: none;
        padding: 10px;
        outline: none;
    }
}
@media only screen and (min-width:500px) and (max-width:4000px){
    .field-group label {
        width: 15%;
    }
    .field-group input , .field-group option , .field-group select {
        font-size: 25px;
        font-family: 'Nunito', sans-serif;
        border-radius: 2px ;
        box-shadow: 0px 2px rgb(8, 5, 5);
        margin-left: 20%;
        width: 50%;
        padding: 10px;
    }
}
 
textarea:focus, input:focus{
    outline: none;
}

